.edit-account-container {
    margin: auto;
    @include flex-column;
    justify-content: center;
    gap: 20px;
}

.edit-account {
    @include flex-column;
    gap: 20px;
    height: 100%;

    .actions {
        margin-top: auto;
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 10px;

        @media only screen and (min-width: 412px) {
            flex-direction: row;
            align-items: center;
            justify-content: flex-end;
            gap: 20px;
        }

        .button {
            margin-left: auto;
            width: fit-content;
        }

        .button.text {
            padding: 8px;

            @media only screen and (min-width: 768px) {
                padding: 0;
            }
        }
    }
}
