.install-app-instruction {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: $z-index-big-9;
    padding: 16px 16px 60px;
    width: 100%;
    height: 100svh;
    background-color: $background-color;

    .swiper {
        height: 100%;
    }

    .swiper-slide {
        display: flex;
        flex-direction: column;
        background-color: $background-color;
    }

    .swiper-pagination {
        margin-bottom: -28px;
    }

    .swiper-pagination-bullet {
        background-color: $light-color;
        opacity: 1;
    }

    .swiper-pagination-bullet-active {
        width: 16px;
        border-radius: $radius-round;
        background-color: $primary-color;
    }

    .MuiSvgIcon-root {
        position: absolute;
        top: 50%;
        right: 20px;
        z-index: $z-index-md-1;
        transform: translate(-30px, -100%);
        opacity: 0;
        width: 120px;
        height: 120px;
        color: $primary-color;
        animation: swipeLeft 2.5s forwards ease-in-out;
    }

    .button {
        margin: 18px 0 0 auto;
    }
}

.install-app-instruction_title {
    margin-bottom: 16px;
    font-size: 18px;
    font-weight: 700;
    text-align: center;
    color: $light-color;

    @media only screen and (min-width: 360px) {
        font-size: 20px;
    }
}

.install-app-instruction_img {
    position: relative;
    flex: 1;
    border: 2px solid $special-color;
    border-radius: $radius-md;
    overflow: hidden;

    img {
        object-fit: contain;
    }
}

.install-app-instruction_browser {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 4px 8px;
    width: calc(100% - 44px);
    display: flex;
    align-items: center;
    gap: 8px;
    border-radius: $radius-sm;
    background-color: $secondary-light-color;
    box-shadow: 0 0 6px 6px $secondary-light-color;

    p {
        font-size: 14px;
        font-weight: 700;
        color: $primary-color;

        @media only screen and (min-width: 360px) {
            font-size: 16px;
        }
    }

    img {
        width: 28px;
        height: 28px;
        object-fit: contain;
    }
}

.install-app-instruction_bg {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: $z-index-1;
    width: 100%;
    height: 100%;
    background-color: #19191999;
    animation: 0.3s fadingBg 2.5s forwards ease-in-out;
}
